import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';

export function goBackAction(code)
{
    return {
        type: IConstant.Actions.SCENIC.DETAIL.GO_BACK,
        code,
    };
}

// 景区详情
export function asyncGetDetailAction(code)
{
    return function(dispatch, getState)
    {
        dispatch(_getDetailRequestAction(code));
        return _fetchDetail(code).then((resp)=>{
            resp = (resp && resp.results && resp.results.length > 0) ? resp.results[0] : resp;
            dispatch(_getDetailSuccessAction(code, resp));
        }).catch((error)=>{
            dispatch(_getDetailFailureAction(code, error));
        });
    };
}

// 门票列表
export function asyncGetTicketListAction(code)
{
    return function(dispatch, getState)
    {
        dispatch(_getTicketListRequestAction(code));
        return _fetchTicketList(code).then((resp)=>{
            resp = resp ? resp.results : [];
            dispatch(_getTicketListSuccessAction(code, resp));
        }).catch((error)=>{
            dispatch(_getTicketListFailureAction(code, error));
        });
    };
}

// 景点列表
export function asyncGetSceneryListAction(code)
{
    return function(dispatch, getState)
    {
        dispatch(_getGetSceneryListRequestAction(code));
        return _fetchGetSceneryList(code).then((resp)=>{
            resp = resp ? resp.results : [];
            dispatch(_getGetSceneryListSuccessAction(code, resp));
        }).catch((error)=>{
            dispatch(_getGetSceneryListFailureAction(code, error));
        });
    };
}

// 景区详情页面滚动条位置
export function setDetailScrollerPositionAction(code, offset)
{
    return {
        type: IConstant.Actions.SCENIC.DETAIL.SET_DETAIL_SCROLL_OFFSET,
        code,
        scrollViewOffset: offset,
    };
}

// 景点列表页面滚动条位置
export function setSceneryListScrollerPositionAction(code, offset)
{
    return {
        type: IConstant.Actions.SCENIC.DETAIL.SET_SCENERY_LIST_SCROLL_OFFSET,
        code,
        scrollViewOffset: offset,
    };
}

export function setSceneryDetailScrollerPositionAction(scenicCode, sceneryCode, offset)
{
    return {
        type: IConstant.Actions.SCENIC.DETAIL.SET_SCENERY_DETAIL_SCROLL_OFFSET,
        code: scenicCode,
        sceneryCode,
        scrollViewOffset: offset,
    };
}

function _fetchGetSceneryList(code)
{
    let url = HttpClient.HOST_E_BUSINESS + '/api/Attractions/GetSceneryList';
    let kvDict = {type: 'app', Code: code};
    // url = 'http://10.9.100.220:8081/api/Attractions/GetSceneryList?Code=JQ465500000003&type=app';
    // kvDict = {};

    return HttpClient.getData(url, kvDict)
        .then((resp) => resp.json());
}

function _getGetSceneryListRequestAction(code)
{
    return {
        type: IConstant.Actions.SCENIC.DETAIL.GET_SCENERY_LIST_REQUEST,
        code,
    };
}

function _getGetSceneryListSuccessAction(code, resp)
{
    return {
        type: IConstant.Actions.SCENIC.DETAIL.GET_SCENERY_LIST_SUCCESS,
        code,
        sceneryList: resp,
    };
}

function _getGetSceneryListFailureAction(code, error)
{
    return {
        type: IConstant.Actions.SCENIC.DETAIL.GET_SCENERY_LIST_FAILURE,
        code,
        message: error
    };
}

function _fetchTicketList(code)
{
    let url = HttpClient.HOST_E_BUSINESS + '/api/Attractions/GetTicketList';
    let now = new Date();
    let date = now.getFullYear() + '-' + (now.getMonth() + 1) + '-' + now.getDate();
    let kvDict = {type: 'app', Code: code, selecttime: date};
    return HttpClient.getData(url, kvDict)
        .then((resp) => resp.json());
}

function _getTicketListRequestAction(code)
{
    return {
        type: IConstant.Actions.SCENIC.DETAIL.GET_TICKET_LIST_REQUEST,
        code,
    };
}

function _getTicketListSuccessAction(code, resp)
{
    return {
        type: IConstant.Actions.SCENIC.DETAIL.GET_TICKET_LIST_SUCCESS,
        code,
        ticketList: resp
    };
}

function _getTicketListFailureAction(code, error)
{
    return {
        type: IConstant.Actions.SCENIC.DETAIL.GET_TICKET_LIST_FAILURE,
        code,
        message: error
    };
}

function _fetchDetail(code)
{
    let url = HttpClient.HOST_E_BUSINESS + '/api/Attractions/GetAttractionsDetail';
    let kvDict = {type: 'app', Code: code};
    // url = 'http://10.9.100.220:8081/api/Attractions/GetAttractionsDetail?Code=JQ465500000003&type=app';
    // kvDict = {};
    return HttpClient.getData(url, kvDict)
        .then((resp) => resp.json());
}

function _getDetailRequestAction(code)
{
    return {
        type: IConstant.Actions.SCENIC.DETAIL.GET_DETAIL_REQUEST,
        code,
    };
}

function _getDetailSuccessAction(code, resp)
{
    return {
        type: IConstant.Actions.SCENIC.DETAIL.GET_DETAIL_SUCCESS,
        code,
        detailItem: resp
    };
}

function _getDetailFailureAction(code, error)
{
    return {
        type: IConstant.Actions.SCENIC.DETAIL.GET_DETAIL_FAILURE,
        code,
        message: error
    };
}


