import React, { Component } from 'react';

export default class SceneryListView extends Component
{
    // 每行3列
    columnCount = 3;
    render()
    {
        return this.renderSceneryListView();
    }
    renderSceneryListView()
    {
        if(!this.props.sceneryList || this.props.sceneryList.length === 0)
        {
            return null;
        }
        let rows = [];
        let rowLen = Math.ceil(this.props.sceneryList.length / this.columnCount);
        for(let i = 0; i < rowLen; i++)
        {
            rows[i] = this.props.sceneryList.slice(i * this.columnCount, (i + 1) * this.columnCount);
        }
        return rows.map((row, index)=>this.renderSceneryRowView(row, index, index === rows.length - 1));
    }
    renderSceneryRowView(row, index, isLastRow)
    {
        let itemWidth = (window.innerWidth - (this.columnCount + 1) * 12) / this.columnCount;
        let itemHeight = itemWidth * 0.75;
        return (
            <div key={index} 
                className="sceneryListRow"
                style={isLastRow ? {marginBottom: '12px'} : {}}>
                {
                    row.map((item, _index)=><div key={_index} 
                        onClick={()=>{
                            if(this.props.showSceneryDetail)
                            {
                                this.props.showSceneryDetail(item);
                            }
                        }}
                        className="sceneryListRowItem" 
                        style={{width: `${itemWidth}px`, height: `${itemHeight}px`}}>
                        <img className="sceneryListRowImg"
                            src={item.GetImage ? item.GetImage.ImageFrontUrl : ''}
                            style={{width: `${itemWidth}px`, height: `${itemHeight}px`}}
                            alt=''/>
                        <div className="sceneryListRowTitle">{item.CnName}</div>
                    </div>)
                }
            </div>
        );
    }
}


