import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon } from 'antd-mobile';
import ReactDOM from 'react-dom';
import Content from '../../views/common/Content';
import SceneryListView from '../../views/scenic/SceneryListView';

import { 
    setSceneryListScrollerPositionAction,
} from '../../store/actions/scenic/detailAction';

class SceneryListScreen extends Component
{
    constructor(props)
    {
        super(props);
        this.showSceneryDetail = this.showSceneryDetail.bind(this);
    }
    componentDidMount()
    {
        this.restoreScrollViewPosition();
    }
    render()
    {
        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        this.props.history.goBack();
                    }}>
                    景点列表
                </NavBar>
                <Content ref={(view)=>{this.scrollView = view;}}>
                    <SceneryListView sceneryList={this.props.sceneryList}
                        showSceneryDetail={this.showSceneryDetail}/>
                </Content>
            </div>
        );
    }
    
    cacheScrollOffset()
    {
        let offsetY = ReactDOM.findDOMNode(this.scrollView).scrollTop;
        this.props.dispatch(setSceneryListScrollerPositionAction(this.props.match.params.code, offsetY));
    }
    restoreScrollViewPosition()
    {
        if(this.scrollView)
        {
            if(!isNaN(this.props.sceneryListScreenScrollViewOffset))
            {
                this.scrollView.scrollToY(this.props.sceneryListScreenScrollViewOffset);
            }
            else
            {
                this.scrollView.scrollToY(0);
            }
        }
    }
    showSceneryDetail(item)
    {
        this.cacheScrollOffset();
        this.props.history.push(`/scenic/scenery-detail/${this.props.match.params.code}/${item.Code}`);
    }
}

const mapStateToProps = function(state, ownProps)
{
    let obj = state.scenic.detailScreen[ownProps.match.params.code];
    return {
        ...obj,
    };
};

SceneryListScreen = connect(mapStateToProps)(SceneryListScreen);

export default SceneryListScreen;


